export enum GALLERY_EVENTS {
  APP_LOADED = 'APP_LOADED',
  GALLERY_CHANGE = 'GALLERY_CHANGE',
  NEED_MORE_ITEMS = 'NEED_MORE_ITEMS',
  ITEM_ACTION_TRIGGERED = 'ITEM_ACTION_TRIGGERED',
  LOAD_MORE_CLICKED = 'LOAD_MORE_CLICKED',
  NAVIGATION_API_READY = 'NAVIGATION_API_READY',
  CURRENT_ITEM_CHANGED = 'CURRENT_ITEM_CHANGED',
  ON_ITEM_CLICKED = 'ON_ITEM_CLICKED',
  HOVER_SET = 'HOVER_SET',
  ITEM_FOCUSED = 'ITEM_FOCUSED',
  ITEM_LOST_FOCUS = 'ITEM_LOST_FOCUS',
  LAYOUT_CHANGED = 'LAYOUT_CHANGED',
  NAVIGATION_STATUS_CHANGED = 'NAVIGATION_STATUS_CHANGED',
}

export interface EventData {
  index?: number;
  [key: string]: any;
}

export type EventsListenerFunction = (
  eventName: GALLERY_EVENTS,
  eventData: EventData,
  event?: Event,
) => void;
